@import url(http://fonts.cdnfonts.com/css/elianto);
.elianto {
  font-family: "Elianto", sans-serif;
  font-weight: bold;
}

.title3 {
  display: inline-block;
  color: #05f2db;
  position: relative;
  white-space: nowrap;
  top: 0;
  left: 0;
  
  /*animation*/
  -webkit-animation: move 5s;
  animation: move 5s;
  /*animation-iteration-count*/
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  /*animation-delay*/
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
@keyframes move {
  0% {
    top: 0px;
  }
  20% {
    top: -50px;
  }
  40% {
    top: -100px;
  }
  60% {
    top: -150px;
  }
  80% {
    top: -200px;
  }
}

@-webkit-keyframes move {
  0% {
    top: 0px;
  }
  20% {
    top: -50px;
  }
  40% {
    top: -100px;
  }
  60% {
    top: -150px;
  }
  80% {
    top: -200px;
  }
}
